"use strict";

import factory from "saw-view";
import config from "saw-config";
import FileViewFinder from "saw-view/finder/file-view-finder";
import ViewEngineResolver from "saw-view/engines/view-engine-resolver";
import HtmlViewEngine from "saw-view/engines/html-view-engine";
import CompiledViewEngine from "saw-view/engines/compiled-view-engine";
import BladeViewCompiler from "saw-view/compilers/blade-view-compiler";

class Bootstrapper {
    static bootstrap(app) {
        factory.finder = new FileViewFinder();
        factory.finder.paths(config.get("view.base_path"));

        factory.finder.extensions.push("html");
        factory.finder.extensions.push("blade.html");

        factory.engine_resolver = new ViewEngineResolver();
        factory.engine_resolver.engines["html"] = new HtmlViewEngine();

        var compiler = new BladeViewCompiler(config.get("view.compile_path"));
        factory.engine_resolver.engines["blade"] = new CompiledViewEngine(factory, compiler);

        factory.extensions["html"] = "html";
        factory.extensions["blade.html"] = "blade";
    }
}

export default Bootstrapper;